
body{
margin: 0 0 0 0;
}
.header{
margin: 0%;
padding: 0;
width: 100%;
height: 10vh;
min-height: 100px;
background-color: #283747 ;
color:#E5E7E9 ;
display: flex;
justify-content: space-between;
align-items: center;
}
.header h1{
width: 20%;
text-align: center;
}
.header nav {
height: 100%;
width: 45%;
display: flex;
justify-content: space-around;
align-items: center;
font-size: 20px;
}
.header nav button{
color: #E5E7E9;
background: #283747;
border: 1px solid #E5E7E9;
border-radius: 5px;
padding: 10px 20px;
}
.header nav button:hover{
color: #283747;
background: #E5E7E9;
border: 1px solid #E5E7E9;
}
.main-component{
height: 75vh;
margin: 0%;
padding: 0%;
background: #FDFEFE ;
}
.footer{
margin: 0;
padding: 0;
width: 100%;
height: 10vh;
min-height: 100px;
background-color: #283747;
color: #E5E7E9;
}
.footer h2{
width: 100%;
height: 100%;
display: flex;
justify-content: center;
align-items: center;
}

#User{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
#User h2{
    font-size: 50px;
    color: #283747;
}
#User span{
    color: crimson;
    font-weight: bold;
}

#UserBalance{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#UserBalance h2{
    font-size: 50px;
    color: #283747;
}
#UserBalance span{
    color: crimson;
    font-weight: bold;
}
